import React from 'react';
import { Link } from 'react-router-dom';

class Menubar extends React.Component {
  render() {
    return(
      <ul className="nav justify-content-center" id="menu">

      <li className="nav-item">
        <Link to={"/"} className = "text-dark nav-link" aria-current="page"></Link>
      </li>
      <li className="nav-item">
        <Link to={"/category/article//0"} className = "text-dark nav-link active"> </Link>
      </li>
      <li className="nav-item">
        <Link to={"/category/precedent//"} className = "text-dark nav-link" aria-current="page"> </Link>
      </li>
      <li className="nav-item">
        <Link to={"/recommand"} className = "text-dark nav-link" aria-current="page"> </Link>
      </li>
    </ul>
    )
  }
}

export default Menubar;